import React from "react";
import { Button, Space, Drawer } from "antd";
import { MenuOutlined } from "@ant-design/icons";

import { useIsMobile } from "../../../hooks";

const NAV_ITEMS = [
  {
    link: "/#about",
    text: "Обо мне",
  },
  {
    link: "/#tech",
    text: "Технологии",
  },
  {
    link: "/#contacts",
    text: "Контакты",
  },
  {
    link: "/cv.pdf",
    text: "CV",
  },
];

export const Menu = () => {
  const { isMobile } = useIsMobile();
  const [isVisibleDrawer, setIsVisibleDrawer] = React.useState<boolean>(false);

  const showDrawer = () => setIsVisibleDrawer(true);
  const handleCloseDrawer = () => setIsVisibleDrawer(false);

  if (isMobile) {
    return (
      <>
        <Button onClick={showDrawer}>
          <MenuOutlined />
        </Button>
        <Drawer visible={isVisibleDrawer} onClose={handleCloseDrawer}>
          <Space direction="vertical" size={10}>
            {NAV_ITEMS.map((item) => (
              <Button
                type="link"
                href={item.link}
                key={item.link}
                onClick={handleCloseDrawer}
              >
                {item.text}
              </Button>
            ))}
          </Space>
        </Drawer>
      </>
    );
  }

  return (
    <>
      {NAV_ITEMS.map((item) => (
        <Button type="link" href={item.link} key={item.link}>
          {item.text}
        </Button>
      ))}
    </>
  );
};
